div#header {
    z-index: 50;
    position: sticky;
    top: 0;
    width: 100%;
    display: grid;
    grid-auto-flow: column;
    width: 100%;
    transition: 0.3s;
}

div.headerPad {
    padding: 0.5em 0em;

}

div.headerBorder {
    align-items: center;
    border-radius: 0px 0px 1em 1em;
    padding-top: 0.1em;
    background-color: white;
    background-image: linear-gradient(-90deg, #000000, #000709f0, #011e33e3);
    /* background: rgb(1, 19, 43); */
    color: aliceblue;
    box-shadow: var(--boxShadowHeader);
}

.activePage {
    color: var(--logoBlue);
    font-weight: 600;
}

div.headerBorder div ul a li {
    border-color: transparent;
}

div#menuLogo {
    padding: 0.2em 0.3em;
}

div#menuLogo img {
    width: 1.7em;
    height: 1.5em;
}

ul#menuCategory {
    list-style-type: none;
    font-size: 0.9em;
}

ul#menuCategory li {
    padding: 0.3em 1em;
    cursor: pointer;
    /* border-bottom: 1.5px solid transparent; */
}

ul#menuCategory li:hover {
    /* opacity: 0.8; */
    font-weight: 600;

}