body {
  margin: 0em;
    position: relative;
    font-family: var(--fontFamily);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  font-size: var(--fontSize);
    direction: rtl;
    color: var(--themeColorText);
    background: var(--themeColorBG);
  }
  
  .background {
    position: fixed;
    right: 0;
    left: 0;
    z-index: -1;
    background-image: url('../assets/img/IMGbg.jpg');
    background-repeat: no-repeat;
    background-size: auto;
    background-size: 100vw 120vh;
    background-attachment: fixed;
    filter: blur(3px);
    height: 100vh;
    width: 100vw;
  }
  
  .background2 {
    position: fixed;
    right: 0;
    left: 0;
    z-index: -1;
    background-image: -webkit-linear-gradient(90deg, rgba(1, 57, 66, 0.908), rgba(2, 44, 51, 0.447), rgba(2, 44, 51, 0.247), transparent);
    background-repeat: no-repeat;
    background-size: auto;
    background-size: 100vw 120vh;
    background-attachment: fixed;
    filter: blur(3px);
    height: 100vh;
    width: 100vw;
  }
  
  .App {
    font-size: 1rem;
  }
  
  div#aboutUs p {
    width: 70vw;
}