:root {
    --fontSize: 20px;

    --logoBlueDeep: #262161;
    --logoBlue: #00ADF0;
    --logoBlueLight: #82E8DE;

    --themeColor: rgb(0, 166, 255);
    --themeColorAlpha: rgba(0, 166, 255, 0.3);

    --themeColorText: black;
    --themeColorBG: white;

    --themeBorderRadius: 15px;

    --fontFamily: Assistant, sans-serif;

    --boxShadowHeader: 0px 1px 4px -1px #222;
    --boxShadow: 1px 1px 5px rgba(0, 0, 0, .955);

    --linearGradient: -45deg, #9c78db5c, transparent;
}